import s from "./Paginator.module.css";
import {useState} from "react";

const Paginator = ({totalItemsCount, usersCount, onPageChange, currentPage, portionSize = 10}) => {
    let pagesCount = Math.ceil(totalItemsCount / usersCount);

    let pages = [];

    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionBorder = (portionNumber - 1) * portionSize + 1;
    let rightPortionBorder = portionNumber * portionSize;

    return (
        <div className={s.paginator}>
            <button onClick={() => {
                setPortionNumber(portionNumber - 1)
            }} className={!(portionNumber > 1) ? s.disabledButton : s.activeButton} disabled={!(portionNumber > 1)}><b>{"<"}</b></button>
            {pages.filter(p => p >= leftPortionBorder && p <= rightPortionBorder)
                .map(p => {
                    return <span onClick={() => {
                        onPageChange(p)
                    }}
                                 className={currentPage === p ? s.activePage : s.pages}
                                 key={p.id}>{p} </span>
                })}
            <button onClick={() => {
                setPortionNumber(portionNumber + 1)
            }} className={!(portionCount > portionNumber) ? s.disabledButton : s.activeButton} disabled={!(portionCount > portionNumber)}><b>{">"}</b></button>
        </div>
    )
}

export default Paginator;