import s from "../Users.module.css";
import {NavLink} from "react-router-dom";
import userPhoto from "../../../assets/images/user.png";
import React from "react";

const User = ({user, followingInProgress, follow, unfollow, isAuth}) => {
    return (
        <div className={s.box}>
            <div className={s.head}>
                <NavLink to={isAuth ? ('/profile/' + user.id) : '/login'}><img
                    src={user.photos.small != null ? user.photos.small : userPhoto}/></NavLink>
                {user.followed
                    ? <button disabled={followingInProgress.some(id => id === user.id)}
                              onClick={() => {
                                  unfollow(user.id);
                              }}
                              className={followingInProgress.some(id => id === user.id)
                                  ? s.disabledFollowButton
                                  : s.activeFollowButton}>Unfollow</button>
                    : <button disabled={followingInProgress.some(id => id === user.id)}
                              onClick={() => {
                                  follow(user.id)
                              }}
                              className={followingInProgress.some(id => id === user.id)
                                  ? s.disabledFollowButton
                                  : s.activeFollowButton}>Follow</button>}
            </div>
            <div className={s.about}>
                <div>{user.name}</div>
                <div>{user.status}</div>
            </div>
        </div>
    )
}

export default User;