import React from "react";
import Header from "./Header";
import {connect} from "react-redux";
import {logout, me} from "../../Redux/auth-reducer";
import {getProfile} from "../../Redux/profile-reducer";

class HeaderContainer extends React.Component {
    state = {
        ownerPhoto: null
    }

    render() {
        if (this.state.ownerPhoto === null) {
            this.props.getProfile(this.props.userId)
                .then(() => {
                    this.props.profile && this.setState({
                        ownerPhoto: this.props.profile.photos.large
                    });
                });
        }
        return <Header {...this.props} ownerPhoto={this.state.ownerPhoto}/>
    }
}

let mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth,
    login: state.auth.login,
    profile: state.profilePage.profile,
    userId: state.auth.userId
})

export default connect(mapStateToProps, {logout, getProfile, me})(HeaderContainer)