import {reduxForm} from "redux-form";
import {createField, Input} from "../common/FormControl/FormControl";
import {required} from "../../utils/validators/validators";
import {connect} from "react-redux";
import {login} from "../../Redux/auth-reducer";
import {Redirect} from "react-router-dom";
import s from "./../common/FormControl/FormControl.module.css";

const LoginForm = ({handleSubmit, error, captcha}) => {
    return (
        <form onSubmit={handleSubmit}>
            <div>Email:</div>
            {createField('email', 'email', Input, [required])}
            <div>Password:</div>
            {createField('password', 'password', Input, [required], {type: "password"})}
            <div>Remember me</div>
            <div className={s.rememberMeField}>
                {createField(null, 'rememberMe', Input, [], {type: "checkbox"})}
            </div>
            {captcha && <div>
                <img src={captcha}/>
                <div>
                    <div className={s.captchaText}>Enter captcha:</div>
                    {createField("captcha", 'captcha', Input, [required])}
                </div>
            </div>
            }
            <div>
                {error && <div className={s.formSummaryError}>
                    {error}
                </div>}
            </div>
            <div>
                <button>Sign In</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm(
    {
        form: 'login'
    }
)(LoginForm)

const Login = (props) => {
    let onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe, formData.captcha);
    }

    if (props.isAuth)
        return <Redirect to={"/profile"}/>

    return <div className={s.loginBlock}>
        <div className={s.signInText}>Sign In</div>
        <div className={s.signInForm}>
            <LoginReduxForm onSubmit={onSubmit} captcha={props.captcha}/>
        </div>
    </div>
}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth,
    captcha: state.auth.captcha
})

export default connect(mapStateToProps,
    {
        login
    }
)(Login);