import {reduxForm} from "redux-form";
import {createField, Input, Textarea} from "../../../common/FormControl/FormControl";
import s from "./ProfileDataForm.module.css";
import ss from './../../../common/FormControl/FormControl.module.css';

const ProfileDataForm = ({handleSubmit, profile, error}) => {
    return <form onSubmit={handleSubmit}>
        <div>
            <button className={s.editModeButton}>save</button>
        </div>
        <div>
            {error && <div className={ss.formSummaryError}>
                {error}
            </div>}
        </div>
        <div className={s.input}>
            <b>Full name</b>: {createField("Full name", "fullName", Input, [])}
        </div>
        <div className={s.boxCheckbox}>
            <b>Looking for a job</b>:
            {createField("", "lookingForAJob", Input, [], {type: "checkbox"})}
        </div>
        <div>
            <b>Professional skills</b>:
            {createField("Professional skills", "lookingForAJobDescription", Textarea, [])}
        </div>
        <div>
            <b>About me</b>: {createField("About me", "aboutMe", Textarea, [])}
        </div>
        <div>
            <b>Contacts</b>: {Object.keys(profile.contacts).map(key => {
            return <div key={key} className={s.contact}>
                <b>{key}: {createField(key, "contacts." + key, Input, [])}</b>
            </div>
        })}
        </div>
    </form>
}

export default reduxForm({form: "edit-profile"})(ProfileDataForm);