import React from "react";
import s from './Header.module.css';
import {NavLink} from "react-router-dom";
import {useState} from "react";
import logoPhoto from "./../../assets/images/logo.png";
import signInPhoto from "./../../assets/images/signIn.jpg";

const Header = (props) => {
    let [showLogout, setShowLogout] = useState(false)

    return <header className={s.header}>
        <img src={logoPhoto}/><b className={s.logoText}>REACT APP</b>
        <div className={s.login}>
            {props.isAuth
                ? <div className={s.login}>
                    {showLogout
                        ? <span className={s.buttonSpan}><button onClick={props.logout}>Logout</button></span>
                        : <b className={s.moreInfo}>{props.login}</b>}
                    <img src={props.ownerPhoto}
                         onClick={() => setShowLogout(showLogout ? false : true)}/>
                </div>
                : <NavLink to={'/login'}>
                    <span className={s.loginButtonRoute}>
                        <img src={signInPhoto}/>
                    </span>
                </NavLink>}
        </div>
    </header>
}

export default Header;