import {useState} from "react";
import s from "./Settings.module.css"
import ProfileDataForm from "../Profile/ProfileInfo/ProfileDataForm/ProfileDataForm";
import Preloader from "../common/Preloader/Preloader";

const Settings = ({profile, saveProfile, getOwnerProfileData, userId}) => {
    let [editMode, setEditMode] = useState(false);
    let [successMode, setSuccessMode] = useState(false)

    let onSubmit = (formData) => {
        saveProfile(formData)
            .then(() => {
                setSuccessMode(true);
                setTimeout(() => {
                    setSuccessMode(false);
                }, 3000)
            });
    }

    return <div>
        <div>
            <div className={successMode && s.successMode}>
                <h2 className={s.settingName}
                    onClick={() => {
                        !editMode ? setEditMode(true) : setEditMode(false);
                        !editMode && getOwnerProfileData();
                    }}>
                    Profile info settings
                </h2>
                {editMode && (
                    userId === profile.userId
                        ? <ProfileDataForm initialValues={profile} onSubmit={onSubmit} profile={profile}/>
                        : <Preloader/>
                )}
            </div>
        </div>
    </div>
}

export default Settings;